import React, { Component } from 'react';
import './resume.css';

class Resume extends Component {
  constructor() {
    super();
    this.state  = {
      password: '', // 첫번째 패스워드
      rePassword: '', // 두번째 패스워드
      pMessage:'패스워드를 입력하세요.', // 확인 메시지 (패스워드 일치여부를 사용자에게 알려주는 메시지)
      show: false,
      blindStyle: {
        width:'100%'
      }
    };  // 초기 state값을 셋팅해준다. 빈 스트링 값은 false를 뜻한다.
  }
  handleConfirmPassword = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    })
    if (e.target.value !== '1234') {
      this.setState({
        pMessage: "패스워드가 일치하지 않습니다.",
        // password: '',
      }) //사용자가 입력한 값이 재확인 비번과 일치하지 않을 경우
    } else {
      this.setState({
        blindStyle: {
          width:'0%'
        }
        // password: '',
      }) //사용자가 입력한 값이 재확인 비번과 일치하지 않을 경우

    }
    console.log('password--', this.state.password);
  }
  render() {
    const cardStyle = {
      borderLeft: '0px',
      borderRight: '0px',
    }
    const borderNone = {
      border: 'none'
    }
    const alignCenter = {
      textAlign: 'center',
      verticalAlign: 'middle'
    }
    const theadStyle = {
      borderBottom: '1px'
    }
    const bilndInputStyle = {
      width: '30%',
      display: 'inline-block'
    }
      return (
        <div>
  
        <div id="myNav" className="overlay" style={this.state.blindStyle}>
          <div className="overlay-content">
          <input type="password" name="password" className="form-control" 
              style={bilndInputStyle}
              value={this.state.password}
              onChange={this.handleConfirmPassword} />
              <span className="message-p">{this.state.pMessage}</span>
          </div>
        </div>
          <div class="container">
            {/* <div className="content-dimmed"></div> */}
            <div class="card" style={cardStyle}>
              <div class="row g-0">
                <div class="col-md-2">
                  <div className="card-body">
                    <h5 class="card-title">Profile</h5>
                  </div>
                </div>
                <div class="col-md-10">
                  <div class="card-body">
                    <p class="card-text">This is a wider card with supporting text below as a natural lead-in to additional content. This content is a little bit longer.</p>
                    {/* <p class="card-text"><small class="text-muted">Last updated 3 mins ago</small></p> */}
                  </div>
                </div>
              </div>
            </div>
            <div class="card" style={cardStyle}>
              <div class="row g-0">
                <div class="col-md-2">
                  <div className="card-body">
                    {/* <h5 class="card-title">Work Experience(technical)</h5> */}
                    <h5 class="card-title">Skills</h5>
                  </div>
                </div>
                <div class="col-md-10">
                  <div class="card-body">
                    <div className="row">
                      <div className="resume">
  
                    <table className="table table-responsive align-middle table-bordered">
                      <thead style={theadStyle}>
                        <tr className="align-items-center" style={alignCenter}>
                          <th rowSpan="2" scope="col">Project</th>
                          <th rowSpan="2" scope="col">Experience</th>
                          <th colSpan="3" scope="colgroup" style={borderNone}>development</th>
                        </tr>
                        <tr style={alignCenter}>
                          <th scope="col">Tool</th>
                          <th scope="col">DBMS</th>
                          <th scope="col">WAS</th>
                        </tr>
                      </thead>
                      <tbody>
                     
                        <tr>
                          <td>d</td>
                          <td>d</td>
                          <td>d</td>
                          <td>d</td>
                          <td>d</td>
                        </tr>
                      
                      </tbody>
                    </table>
                    </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="card" style={cardStyle}>
              <div class="row g-0">
                <div class="col-md-2">
                <div className="card-body">
                  <h5 class="card-title">Education</h5>
                </div>
                </div>
                <div class="col-md-10">
                  <div class="card-body">
                    <p class="card-text">This is a wider card with supporting text below as a natural lead-in to additional content. This content is a little bit longer.</p>
                    {/* <p class="card-text"><small class="text-muted">Last updated 3 mins ago</small></p> */}
                  </div>
                </div>
              </div>
            </div>
          </div>
          </div>
      );
  }

};

export default Resume;